.vs-reset {
  text-align: left;
  box-sizing: border-box;
  text-size-adjust: 100%;
  color: #4a5568;
  font-size: 16px;
  font-weight: 400;
  font-family: "system-ui", "-apple-system", "BlinkMacSystemFont", '"Helvetica Neue"', "Arial", "sans-serif";
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }
  .vs-reset *, .vs-reset *:after, .vs-reset *:before {
    box-sizing: border-box; }
  .vs-reset button,
  .vs-reset input,
  .vs-reset select,
  .vs-reset textarea {
    font-family: "system-ui", "-apple-system", "BlinkMacSystemFont", '"Helvetica Neue"', "Arial", "sans-serif"; }
  .vs-reset a {
    cursor: pointer;
    text-decoration: none; }
  .vs-reset ul {
    list-style: none; }
  .vs-reset strong {
    font-weight: 600; }

.vs-select.vs-open-up .vs-dropdown {
  bottom: 100%; }

.vs-select.vs-open-down .vs-dropdown {
  top: 100%; }

.vs-dropdown {
  width: 100%;
  margin-top: 8px;
  position: absolute;
  border-radius: 4px;
  margin-bottom: 8px;
  box-sizing: content-box;
  z-index: 20;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 4px 11px;
  background-color: #fff; }

.vs-dropdown-scroll {
  overflow-y: auto;
  padding-top: 4px;
  position: relative;
  padding-bottom: 4px;
  max-height: 300px; }

.vs-dropdown-loader,
.vs-dropdown-no-options {
  text-align: center;
  padding: 6px 10px; }

.vs-dropdown-option {
  width: 100%;
  display: block;
  cursor: pointer;
  user-select: none;
  font-size: inherit;
  background-color: transparent;
  color: #2d3748;
  padding: 6px 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  .vs-dropdown-option.focused:not(.disabled) {
    background-color: #ebf8ff; }
  .vs-dropdown-option.selected {
    color: #fff;
    background-color: #4299e1; }
    .vs-dropdown-option.selected.focused {
      color: #fff;
      background-color: #3182ce; }
  .vs-dropdown-option.disabled {
    cursor: default;
    color: #a0aec0;
    background-color: transparent; }

.vs-select {
  position: relative; }
  .vs-select:not(.disabled) .vs-select-control:hover {
    border-color: #718096;
    background-color: #fff; }
    .vs-select:not(.disabled) .vs-select-control:hover .vs-select-arrow:before {
      border-top-color: #718096; }
    .vs-select:not(.disabled) .vs-select-control:hover .vs-select-placeholder {
      color: #718096; }
    .vs-select:not(.disabled) .vs-select-control:hover .vs-select-single {
      color: #1a202c; }
  .vs-select.disabled {
    pointer-events: none; }
    .vs-select.disabled .vs-select-control {
      border-color: #cbd5e0;
      background-color: #fff; }
      .vs-select.disabled .vs-select-control .vs-select-arrow:before {
        border-top-color: #cbd5e0; }
    .vs-select.disabled .vs-select-placeholder {
      color: #cbd5e0; }
    .vs-select.disabled .vs-select-input {
      color: #cbd5e0; }
    .vs-select.disabled .vs-select-single {
      color: #cbd5e0; }

.vs-select-control {
  display: flex;
  cursor: default;
  flex-wrap: wrap;
  position: relative;
  align-items: center;
  justify-content: space-around;
  min-height: 38px;
  border-radius: 4px;
  border: 1px solid #a0aec0;
  background-color: #fff; }

.vs-select-container {
  flex: 1 1 0%;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  position: relative;
  align-items: center;
  padding: 2px 8px; }

.vs-select-placeholder {
  top: 50%;
  z-index: 1;
  margin-left: 2px;
  user-select: none;
  margin-right: 2px;
  position: absolute;
  transform: translateY(-50%);
  color: #a0aec0; }

.vs-select-input {
  z-index: 2;
  margin: 2px;
  flex-grow: 1;
  padding-top: 2px;
  position: relative;
  padding-bottom: 2px;
  color: #2d3748; }
  .vs-select-input input {
    outline: 0;
    padding: 0;
    width: 100%;
    border: 0px;
    color: inherit;
    font-size: inherit;
    background: transparent; }

.vs-select-single-value {
  top: 50%;
  margin-left: 2px;
  overflow: hidden;
  margin-right: 2px;
  position: absolute;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: calc(100% - 8px);
  transform: translateY(-50%);
  color: #2d3748; }

.vs-select-multiple-value {
  margin: 2px;
  min-width: 0;
  display: flex;
  font-size: 0.875em;
  border-radius: 4px;
  background-color: #bee3f8; }
  .vs-select-multiple-value:hover {
    background-color: #bee3f8; }

.vs-select-multiple-value-label {
  overflow: hidden;
  user-select: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #2d3748;
  padding: 3px 3px 3px 6px;
  border-radius: 4px; }

.vs-select-multiple-value-remove {
  display: flex;
  min-width: 27px;
  cursor: pointer;
  position: relative;
  align-items: center;
  justify-content: center;
  padding: 0 4px;
  border-radius: 4px; }
  .vs-select-multiple-value-remove:before, .vs-select-multiple-value-remove:after {
    width: 2px;
    left: 13px;
    content: '';
    height: 10px;
    position: absolute;
    background-color: #2d3748; }
  .vs-select-multiple-value-remove:before {
    transform: rotate(45deg); }
  .vs-select-multiple-value-remove:after {
    transform: rotate(-45deg); }
  .vs-select-multiple-value-remove:hover {
    color: #2d3748;
    background-color: #90cdf4; }
    .vs-select-multiple-value-remove:hover:before, .vs-select-multiple-value-remove:hover:after {
      background-color: #2d3748; }

.vs-select-actions {
  display: flex;
  flex-shrink: 0;
  align-self: stretch;
  align-items: center; }

.vs-select-arrow {
  display: flex;
  cursor: pointer;
  position: relative;
  align-items: center;
  justify-content: center;
  width: calc(38px - 2px);
  height: calc(38px - 2px); }
  .vs-select-arrow:before {
    width: 0;
    height: 0;
    content: '';
    display: block;
    position: absolute;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid #a0aec0; }

.vs-select-loader {
  display: block;
  cursor: pointer;
  position: relative;
  width: calc(38px - 2px);
  height: calc(38px - 2px); }
  .vs-select-loader > div {
    margin: 8px;
    display: block;
    border-width: 2px;
    position: absolute;
    border-style: solid;
    border-radius: 100px;
    width: calc(38px - 18px);
    height: calc(38px - 18px);
    border-color: #cbd5e0 transparent transparent transparent;
    animation: vs-loader 1200ms cubic-bezier(0.5, 0, 0.5, 1) infinite; }
    .vs-select-loader > div:nth-child(1) {
      animation-delay: -450ms; }
    .vs-select-loader > div:nth-child(2) {
      animation-delay: -300ms; }
    .vs-select-loader > div:nth-child(3) {
      animation-delay: -150ms; }

@keyframes vs-loader {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.vs-loader-dots:after {
  content: ' .';
  animation: vs-loader-dots 500ms steps(5, end) infinite; }

@keyframes vs-loader-dots {
  0%, 20% {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0.25rem 0 0 rgba(0, 0, 0, 0), 0.5rem 0 0 rgba(0, 0, 0, 0); }
  40% {
    color: #4a5568;
    text-shadow: 0.25rem 0 0 rgba(0, 0, 0, 0), 0.5rem 0 0 rgba(0, 0, 0, 0); }
  60% {
    text-shadow: 0.25rem 0 0 #4a5568, 0.5rem 0 0 rgba(0, 0, 0, 0); }
  80%, 100% {
    text-shadow: 0.25rem 0 0 #4a5568, 0.5rem 0 0 #4a5568; } }
